/* Chat.css */

/* Main container */
.chat-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 90vh;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    background-color: #e5ddd5;
    border-radius: 10px;
    overflow: hidden;
  }
  
  /* Chat window: Scrollable area for messages */
  .chat-window {
    flex: 1;
    padding: 20px;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
    background-color: #efeae2;
  }
  
  /* Message bubble styles */
  .message-bubble {
    max-width: 60%;
    padding: 10px 15px;
    border-radius: 8px;
    font-size: 15px;
    line-height: 1.4;
    position: relative;
    word-wrap: break-word;
  }
  
  /* Sent message (Right aligned, green bubble) */
  .sent {
    align-self: flex-end;
    background-color: #dcf8c6;
    border-bottom-right-radius: 0;
  }
  
  /* Received message (Left aligned, white bubble) */
  .received {
    align-self: flex-start;
    background-color: #ffffff;
    border-bottom-left-radius: 0;
  }
  
  /* Message timestamp */
  .message-bubble::after {
    content: attr(data-time);
    display: block;
    font-size: 11px;
    color: #999;
    margin-top: 5px;
    text-align: right;
  }
  
  /* Input area container */
  .chat-input-container {
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: #f0f0f0;
    border-top: 1px solid #ccc;
  }
  
  /* Chat input field */
  .chat-input {
    flex: 1;
    padding: 12px;
    font-size: 16px;
    border: none;
    border-radius: 25px;
    background-color: #ffffff;
    outline: none;
    margin-right: 10px;
  }
  
  /* Send button */
  .send-button {
    padding: 10px 15px;
    background-color: #075e54;
    color: white;
    border: none;
    border-radius: 50%;
    cursor: pointer;
  }
  
  .send-button:hover {
    background-color: #128c7e;
  }
  